import '../common/style/app.css';
import 'weui';
import weui from 'weui.js';

import LabaGif from '../common/images/laba.gif';
import LabaPng from '../common/images/laba.png';
import audioControl from '../common/js/audio.js';
import { httpPost, getUrl } from '../common/js/http.js';
import '../common/js/oauth.js';


// oauthCheck();

$(function () {
	// if (process.env.NODE_ENV !== 'production') {
	// console.log('Looks like we are in development mode!');
	// }
	// else{
	// alert('production !') 
	// }	

	$('#audio_img').on('click', function (e) {
		// audioControl(audio, img, imgplay, imgpaused)
		audioControl($('audio')[0], this, LabaGif, LabaPng);
	});

	$('#formSubmitBtn').on('click', function (e) {
		//
		weui.form.validate('#reg_form', function (error) {
			if (!error) {
				var loading = weui.loading('提交中...');
				var userName = $('#userName').val();
				var userSchool = $('#userSchool').val();
				var userPhone = $('#userPhone').val();

				var userObject = {
					'userName': userName,
					'phone': userPhone,
					'userAddr': userSchool,
					'userType': '3'
				};
				httpPost('/user',
					JSON.stringify(userObject),
					function (response) {
						var url = getUrl('/pay.html');
						window.location.replace(url);
					});

			}
		});

	});

	weui.form.checkIfBlur('#reg_form');
})